@import 'setup';

//variables variables variables variables variables variables variables 
$opensans: 'Open Sans', 'Century Gothic', CenturyGothic, AppleGothic, sans-serif;
$mont: 'Montserrat', 'Franklin Gothic Medium', 'Franklin Gothic', 'ITC Franklin Gothic', Arial, sans-serif;
$droid: 'Droid Serif', serif;

$cyan: #4CDDF3;
$grey: #666;

//custom styles custom styles custom styles custom styles custom styles

html{
	font-size: 62.5%;
}

body{
	font-family: $opensans;
}

section{
	position: relative;
	padding: 6rem 0;
}

img{
	max-width: 100%;
	height: auto;
}

//custom classes custom classes custom classes custom classes

.wrapper{
	max-width: 1170px;
	margin: 0 auto;
	justify-content: center;
	align-items: center;
}

.flex{
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}

.column{
	flex-direction: column;
}

.before{
		position: relative;
}

.blueUnderline::before{
	content: " ";
	width: 6rem;
	border: solid 1px $cyan;
	top: -3.6rem;
	left: calc(50% - 3rem);
	position: absolute;
}

.fifty{
	width: 50%;
}

//typography typography typography typography typography typography 

h1{
	text-transform: uppercase;
	font-weight: 100;
	font-size: 10.5rem;
	font-family: $mont;
	letter-spacing: .4rem;
}

h2{
	text-transform: uppercase;
	color: #ccc;
	font-weight: 100;
	font-family: $mont;
	text-align: center;
	font-size: 1.5rem;
	padding: 0;
	margin: 0;
}

h3{
	font-family: $opensans;
	font-size: 2.8rem;
	text-align: center;
	font-weight: 100;
	color: $grey;
	margin-bottom: 4rem;
}

h4{
	font-family: $opensans;
	font-size: 3.5rem;
	text-align: center;
	font-weight: 100;
	color: white;
	margin: 2rem 0 4rem;
}

h6{
	font-style: italic;
	font-weight: 400;
	font-family: $droid;
	letter-spacing: .3rem;
	font-size: 2.6rem;
}

p{
	font-size: 1.5rem;
	line-height: 2;
	color: $grey;
	span{
		color: $cyan;
		font-weight: 600;
	}
}

ul{
	list-style: none;
}

li{
	display: inline-block;
}

a{
	text-decoration: none;
	color: black;
	transition: color .6s;
	font-weight: 600;
	&:focus, &:hover{
		color: $cyan;
	}
}

//buttons buttons buttons buttons buttons buttons buttons buttons buttons 

button{
	background: none;
	border: solid 2px $cyan;
	font-family: $opensans;
	color: white;
	text-transform: uppercase;
	font-size: 1.4rem;
	font-weight: 400;
	padding: 1.2rem 4rem;
	letter-spacing: .4rem;
	.fa{
		margin-left: 1rem;
	}
}

//flickity flickity flickity flickity flickity flickity flickity flickity

.flickity-page-dots{
	bottom: -5rem;
}

.flickity-page-dots .dot{
	background-color: $cyan;
	height: 12px;
	width: 12px;
}

//header header header header header header header header header header

header{
	background: url(/images/topbanner.jpg) top center no-repeat;
	background-size: cover;
	padding: 29rem 0;
	text-align: center;
	color: white;
	h1{
		margin-top: 0;
	}
	p{
		width: 60%;
		margin: 0 auto;
		padding-bottom: 3rem;
		font-size: 1.7rem;
		color: white;
		font-weight: 300;
	}
}

//nav nav nav nav nav nav nav nav nav nav nav nav nav nav nav nav nav

nav{
	background: white;
	bottom: 0;
	width: 100%;
	height: 7rem;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
	padding: 0 6rem;
	a{
		text-transform: uppercase;
		font-size: 1.4rem;
		padding: 0 3rem;
		letter-spacing: .2rem;
		color: $grey;
		&:last-child{
			padding-right: 0;
		}
	}
}

//section.about section.about section.about section.about
section.about{
	.wrapper {
		align-items: flex-start;
	}
	.fifty {
		width: calc(50% - 1.5rem);
		margin: 2rem 0;
		padding: 1rem 2rem;
	}
	.fifty:first-child{
		margin-right: 3rem;
	}
}

//section.add section.add section.add section.add section.add

section.add{
	background: url(/images/topbanner.jpg) center no-repeat;
	background-size: cover;
	.imgDiv{
		img{
			width: 50%;
			display: inline-block;
		}
		
	}
}